@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

body {
    font-family: 'cwTeXYen', sans-serif;
    padding: 0;
    line-height: 1.6;
}

img {
    background-size: cover;
}

a {
    color: white;
}

.appTop {
    background-color: #005824;
    position: fixed;
    padding: 1rem 1rem;
    font-size: 20px;
    bottom: 40px;
    right: 40px;
    color: #fff;
    border-radius: 50%
}