.container {
    position: relative;
    background-color: #e6d5a8;
    height: 50px;
}

#nav_control {
    position: absolute;
    z-index: -2;
}

.nav_logo img {
    position: absolute;
    margin: 5px;
    width: 2.5rem;
    border-radius: 25%;
    background-color: white;
    z-index: 1;
}

.theme_icon {
    position: absolute;
    width: 40px;
    height: 40px;
    right: 10px;
    top: 5px;
    background: transparent;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
    z-index: 1;
}

.nav_menu {
    background-color: #e6d5a8;
    width: 100%;
    text-align: center;
    position: absolute;
    top: -300%;
    transition: .5s;
    /* z-index: -2; */
}

.nav_menu li {
    border-color: #fff;
    border-width: 0.5px;
    border-top-style: solid;
}

#nav_control:checked~.nav_menu {
    top: 50px;
}

/* 電腦尺寸 */
@media screen and (min-width: 768px) {
    .theme_icon {
        display: none;
        opacity: 0;
    }

    .container {
        display: flex;
        align-items: center;
    }

    .nav_logo img {
        position: relative;
        vertical-align: top;
    }

    .nav_menu {
        position: relative;
        display: flex;
        justify-content: flex-end;
        gap: 1rem;
        top: 0;
        left: -20px;
        background-color: transparent;
    }

    .nav_menu li {
        border-color: #e6d5a8;
    }

    #nav_control:checked~.nav_menu {
        top: 0px;
    }
}