.layout_container {
    /* display: grid; */
    /* justify-items: center; */
    /* grid-template-columns: repeat(2, 1fr); */
    /* padding-top: 2.5%; */
    /* padding-bottom: 2.5%; */
    background-image: url('../assets/背景.png');
    padding-bottom: 10px;
}

.layout_card {
    height: 250px;
    display: grid;
    place-items: center;
    background-color: bisque;
    border: 5px;
    border-style: solid;
    border-radius: 8%;
    border-color: #afc819;
    margin: 5%;
    box-shadow: 0 0 10px 1px Gray;
    transition: .7s;
}

.layout_container img {
    width: 85%;
}

.layout_title {
    font-size: 4vw;
}

.layout_detail {
    display: none;
    text-align: left;
    padding-left: 5%;
    padding-right: 5%;
    text-align: justify;
    font-size: 3.5vw;
}

.layout_card:hover img {
    display: none;
    transform: rotateY(0deg);
    transition: .7s;
}

.layout_card:hover .layout_title {
    transform: rotateY(180deg);
    transition: .7s;
}

.layout_card:hover .layout_detail {
    display: block;
    transform: rotateY(180deg);
    transition: .7s;
}

.layout_card:hover {
    transform: rotateY(180deg);
    align-content: center;
    transition: .7s;
}

/* 卡片翻轉文字淡化效果 */
.layout_card:hover div {
    animation: fadeIn .3s;
    animation-delay: .2s;
    animation-fill-mode: backwards;
  }
  @keyframes fadeIn {
    0% {
      opacity: 0;
      transform: translateY(20px) rotateY(180deg);
    }
    100% {
      opacity: 1;
      transform: translateY(0) rotateY(180deg)
    }
  }

/* 電腦尺寸 */
@media screen and (min-width: 768px) {
    .layout_title {
        font-size: 3vw;
    }

    .layout_detail {
        font-size: 2vw;
    }

    .layout_card {
        height: 300px;
    }
}